.form {
  display: flex;
  flex-direction: column;
  width: 300px;
  gap: 20px;
  margin-left: 20px;
}

.label {
  margin-left: 20px;
  font-weight: 700;
}

.btn {
  width: 70px;
  background-color: black;
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
}

input {
  padding: 5px;
}

.card-wrapper {
  margin-top: 40px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  margin-left: 8vw;
}

.card {
  width: 300px;
  height: 200px;
  border: 1px solid;
  padding-left: 10px;
  border-radius: 10px;
}

.wrapper {
  margin-left: 7vw;
}

.filter {
  width: 30vw;
  margin-left: 20px;
}

.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  border: 8px solid;
  border-color: #000 #0000;
  animation: l1 1s infinite;
}

@keyframes l1 {
  to {
    transform: rotate(0.5turn);
  }
}